@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;500;700&display=swap);
:root {
  --text-color: #202047;
  --light-text-color: #b9b9ce;
  --white: #ffffff;
}

html {
  box-sizing: border-box;
  font-size: 62.5%; /* 1REM = 10px */
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  font-family: "Inter", sans-serif;
  margin: 0;
  height: 100vh;
  width: 100vw;
}

/* Globals */
h1,
h2 {
  margin: 0;
}
p {
  margin: 0;
}

.App_container__1MQN3 {
  background: url(/coding-bootcamp-testimonials-slider/static/media/pattern-curve.05698a95.svg) no-repeat 0% 100% / 28.5rem
    7.59rem;
  padding: 2.4rem;
  height: 100vh;
  width: 100vw;
  margin: 0 auto;
}

/* Desktop */
@media (min-width: 1440px) {
  .App_container__1MQN3 {
    max-width: 144rem;
    padding: 5rem 7.3rem 9.3rem 16.5rem;
    background-size: 61rem;
  }
}

.Testimonial_testimonial__1MBVP {
  background: url(/coding-bootcamp-testimonials-slider/static/media/pattern-bg.7f46e67e.svg) no-repeat 50% 0 / 100%;
  max-width: 39.8rem;
  height: 100%;
  margin: 0 auto;
}
.Testimonial_user__13v2A {
  padding: 3rem 3.7rem;
  margin-bottom: 3.25rem;
}
.Testimonial_user__13v2A .Testimonial_user-image__1sQ_0 {
  width: 100%;
  display: block;
  margin: 0 auto;
  border-radius: 0.5rem;
}
.Testimonial_slider-container__UGi_a {
  display: flex;
  justify-content: center;
  align-items: center;
}
.Testimonial_slider__oqgkz {
  position: absolute;
  background-color: var(--white);
  width: 8rem;
  height: 4rem;
  display: flex;
  border-radius: 2.8rem;
  box-shadow: 0 1.5rem 2rem -1rem #24247119;
}
.Testimonial_slider__oqgkz .Testimonial_arrow-container__3EYXn {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.Testimonial_slider__oqgkz img {
  height: 1.4rem;
}
.Testimonial_testimonial-text__1FEPq {
  background: url(/coding-bootcamp-testimonials-slider/static/media/pattern-quotes.219bd182.svg) no-repeat 50% 0 / 6rem 5rem;
  padding-top: 2.5rem;
  text-align: center;
}
.Testimonial_testimonial-text__1FEPq p {
  font-size: 1.8rem;
  font-weight: 300;
  line-height: 2.4rem;
  color: var(--text-color);
  margin-bottom: 1.9rem;
}
.Testimonial_user-data__1ZHMn h1,
.Testimonial_user-data__1ZHMn h2 {
  line-height: 2rem;
  font-size: 1.5rem;
}
.Testimonial_user-data__1ZHMn h1 {
  font-weight: 700;
  color: var(--text-color);
}
.Testimonial_user-data__1ZHMn h2 {
  font-weight: 500;
  color: var(--light-text-color);
}

/* Desktop */
@media (min-width: 1440px) {
  .Testimonial_testimonial__1MBVP {
    max-width: none;
    max-width: initial;
    padding: 6.3rem 9.2rem 0 0;
    background-size: 69.7rem;
    background-position: 100% 0;
  }
  .Testimonial_user__13v2A {
    padding: 0;
    margin-bottom: 0;
  }
  .Testimonial_user__13v2A .Testimonial_user-image__1sQ_0 {
    width: 54rem;
    margin: 0 0 0 auto;
  }
  .Testimonial_slider-container__UGi_a {
    width: 54rem;
    margin-left: auto;
    justify-content: flex-start;
  }
  .Testimonial_slider__oqgkz {
    margin-left: 6.2rem;
    width: 11.2rem;
    height: 5.6rem;
  }
  .Testimonial_slider__oqgkz img {
    height: 1.8rem;
    transition: 250ms ease-in-out;
  }
  .Testimonial_slider__oqgkz .Testimonial_arrow-container__3EYXn:hover img {
    transform: scale(1.2);
  }
  .Testimonial_testimonial-text__1FEPq {
    position: absolute;
    z-index: 10;
    max-width: 63.5rem;
    text-align: left;
    margin-top: -43.8rem;
    padding-top: 6.4rem;
    background-size: 12rem 10rem;
    background-position: 9.5rem 0;
  }
  .Testimonial_testimonial-text__1FEPq p {
    font-size: 3.2rem;
    line-height: 4.4rem;
    margin-bottom: 3.2rem;
  }
  .Testimonial_user-data__1ZHMn h1,
  .Testimonial_user-data__1ZHMn h2 {
    line-height: 3.8rem;
    font-size: 2rem;
  }
  .Testimonial_user-data__1ZHMn {
    display: flex;
    grid-column-gap: 1rem;
    column-gap: 1rem;
  }
}

